import React from "react";
import { Card, CardContent, Button, ButtonGroup } from "@mui/material";
import moment from "moment";
import { useHistory } from "react-router-dom";

const JobMain = () => {
  const History = useHistory();

  return (
    <div>
      <div></div>
      <ButtonGroup>
      <Button
        variant="contained"
        onClick={() =>
          History.push("//")
        }>
        
      </Button>
      <Button
        variant="contained"
        onClick={() =>
          History.push("//")
        }>
        
      </Button>
      </ButtonGroup>
    </div>
  );
};

export default JobMain;
