import React from "react";
import { Card, CardContent, Button, Box, Grid } from "@mui/material";
import moment from "moment";
import { useHistory } from "react-router-dom";
import CircularProgress from "@mui/material/CircularProgress";

import img from '../img/test.jpg'

const starttime = 3;

const Home = () => {
  const [currenttime, setTime] = React.useState(0);
  const [load, setLoad] = React.useState(false);
  const History = useHistory();

  React.useState(() => {
    setLoad(false);
    fetch("https://tinyurl.com/gpftimeapi")
      .then((response) => response.json())
      .then((data) => {
        const serverOffset = moment.utc(data.dateTime).diff(new Date());
        setTime(moment().add("milliseconds", serverOffset).unix());
        setLoad(true);
      })
      .catch((error) => console.error("Error:", error));
  }, []);
  return (
    <div className="d-flex justify-content-center">
      <Card
        className="colorbase middlecenter bg-transparent"
        sx={{ boxShadow: 0 }}>
        <CardContent className="row">
          <div className="col-md-7 col-12">
            <img src={img} />
          </div>
          <div className="col-md">
            <Grid container space={2}>
              <Grid item xs='auto'>
                <Button
                  variant="contained"
                  onClick={() => History.push("/")}>
                  
                </Button>
              </Grid>
              {load && currenttime >= starttime && (
                <Grid item md>
                  <Button
                    variant="contained"
                    onClick={() => History.push("/")}>
                    
                  </Button>
                </Grid>
              )}
              {!load && (
                <Grid item md>
                  <Button
                    variant="contained"
                    sx={{ width: { xs: "100%", md: 120 }, height: 45 }}
                    disabled={true}>
                    <CircularProgress sx={{ color: "#F9D8A6" }} size={24} />
                  </Button>
                </Grid>
              )}
            </Grid>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default Home;
