import logo from "./logo.svg";
import React from "react";
import "./App.css";

import {
  AppBar,
  Box,
  Slide,
  Drawer,
  IconButton,
  List,
  ListItemButton,
  ListItemText,
  Toolbar,
  Typography,
  Button,
  ListItem,
  CardMedia,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";

import Home from "./component/home";
import JobMain from "./component/jobmain";
import Job from "./component/jobComponent/job";
import Project from "./component/jobComponent/project";
import Speech from "./component/speech";
import VirtualRun from "./component/virtualrun";
import Gallery from "./component/activitygallery";

import AOS from "aos";

import {
  Route,
  useLocation,
  Link,
  Switch as BasicSwitch,
  useHistory,
  Redirect,
} from "react-router-dom";

const drawerWidth = 240;
const navItemsId = [
  "/",
  "/",
  "",
  "-",
  "",
];
const navItems = [
  "",
  "",
  "",
  "-",
  "",
];

function App() {
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const [menuIndex, setMenuIndex] = React.useState(0);

  const AppBarRef = React.useRef(null);

  const History = useHistory();
  const location = useLocation();

  const [wh, setRealwidth] = React.useState([
    window.innerWidth,
    window.innerHeight,
  ]);
  const [scrollPosition, setScrollPosition] = React.useState(0);
  const handleScroll = () => {
    const position = window.pageYOffset;
    setScrollPosition(position);
  };

  React.useEffect(() => {
    window.scrollTo(0, 0);
    window.addEventListener("scroll", handleScroll, { passive: true });
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  const handleDrawerToggle = () => {
    setMobileOpen((prevState) => !prevState);
  };

  React.useState(() => {
    function handleWindowResize() {
      setRealwidth([window.innerWidth, window.innerHeight]);
    }
    AOS.init({ duration: 0 });
    window.addEventListener("resize", handleWindowResize);
    const path = decodeURI(location.pathname);
    if (path.includes("")) {
      setMenuIndex(1);
    } else if (path.includes("")) {
      setMenuIndex(2);
    } else if (path.includes("-")) {
      setMenuIndex(3);
    } else if (path.includes("")) {
      setMenuIndex(4);
    } else {
      if (path == "/") {
        setMenuIndex(0);
      } else {
        setMenuIndex(-1);
      }
    }
  }, []);

  React.useEffect(() => {
    const path = decodeURI(location.pathname);
    if (path.includes("")) {
      setMenuIndex(1);
    } else if (path.includes("")) {
      setMenuIndex(2);
    } else if (path.includes("-")) {
      setMenuIndex(3);
    } else if (path.includes("")) {
      setMenuIndex(4);
    } else {
      if (path == "/") {
        setMenuIndex(0);
      } else {
        setMenuIndex(-1);
      }
    }
  }, [window.location.pathname]);

  const ChangePage = (i) => {
    // Click menu to change
    setMenuIndex(i);
    History.push(navItemsId[i]);
  };

  const drawer = (
    <Box onClick={handleDrawerToggle} sx={{ textAlign: "center" }}>
      <List>
        {navItems.map((item, i) => (
          <ListItem
            className={
              item == navItems[menuIndex]
                ? "border-primary bg-header text-dark text-bold"
                : ""
            }
            onClick={() => ChangePage(i)}
            key={item}
            disablePadding>
            <ListItemButton sx={{ textAlign: "center" }}>
              <ListItemText primary={item} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Box>
  );

  return (
    <>
      <Box data-aos="fade-in">
        <Slide in={location.pathname != "/"} timeout={200}>
          <AppBar
            ref={AppBarRef}
            component="nav"
            className="appbarcolor"
            style={{ background: "transparent", boxShadow: "none" }}>
            <Toolbar>
              <IconButton
                className="colorbase"
                aria-label="open drawer"
                edge="start"
                onClick={handleDrawerToggle}
                sx={{ mr: 2, display: { sm: "none" } }}>
                <MenuIcon />
              </IconButton>
              <div style={{ flexGrow: 1 }}>
                <CardMedia
                  src="https://www.gpf.or.th/thai2019/assets/logo-n.png"
                  component="img"
                  sx={{ width: 120 }}
                />
              </div>

              <Box sx={{ display: { xs: "none", sm: "block" } }}>
                {navItems.map((item, i) => (
                  <Button
                    component={Link}
                    to={navItemsId[i]}
                    className={
                      item == navItems[menuIndex]
                        ? "border-primary bg-header text-dark"
                        : "colorbase"
                    }
                    onClick={() => ChangePage(i)}
                    key={item}
                    sx={{ color: "#fff" }}>
                    {item}
                  </Button>
                ))}
              </Box>
              <Box sx={{ display: wh[0] < 700 ? "flex" : "none" }}>
                <Typography className="colorbase text-right">
                  {navItems[menuIndex]}
                </Typography>
              </Box>
            </Toolbar>
          </AppBar>
        </Slide>
        <Box component="nav">
          <Drawer
            variant="temporary"
            open={mobileOpen}
            onClose={handleDrawerToggle}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
            sx={{
              display: { xs: "block", sm: "none" },
              "& .MuiDrawer-paper": {
                boxSizing: "border-box",
                width: drawerWidth,
              },
            }}>
            {drawer}
          </Drawer>
        </Box>
        <Box component="main" className="container">
          <Toolbar />
          <BasicSwitch>
            <Route exact path="/">
              <Home />
            </Route>
            <Route exact path="/">
              <JobMain />
            </Route>
            <Route exact path="//">
              <Job />
            </Route>
            <Route exact path="//">
              <Project />
            </Route>
            <Route exact path="/">
              <Speech />
            </Route>
            <Route exact path="/-">
              <VirtualRun />
            </Route>
            <Route exact path="/">
              <Gallery />
            </Route>
            <Route
              path="*"
              render={() => {
                History.push("/");
              }}
            />
          </BasicSwitch>
        </Box>
      </Box>
    </>
  );
}

export default App;
